.catalog {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.catalog-search {
  display: flex;
  align-items: center;
  width: 100%;
}

.catalog-search-results {
  height: 90%;
  margin-top: 8px;
  padding-right: 16px;
  padding-left: 16px;
}

.catalog-item-main {
  display: grid;
  grid-template-columns: 80% 20%;
}

.catalog-item-header {
  display: flex;
  gap: 8px;
}

.catalog-item-topics {
  /* border-left: 1px solid #E7E9E9; */
  display: flex;
  flex-wrap: wrap;
}

.topic-item {
  padding: 4px;
}

.catalog-item.card {
  width: 100%;
  padding: 16px;
  background: white;
  box-shadow: 0px 1px 2px 1px rgba(40, 54, 54, 0.1);
  border-radius: 4px;
  margin-bottom: 8px;
}

